import { Component, OnInit } from '@angular/core';
import { HomePopupService } from '../../services/home-popup.service';

@Component({
  selector: 'app-home-popup',
  templateUrl: './home-popup.component.html',
  styleUrl: './home-popup.component.scss'
})
export class HomePopupComponent implements OnInit {
  name: string = '';
  minutes: number = 0;
  city: string = '';

  constructor(private popupService: HomePopupService) { }

  ngOnInit(): void {
    this.showPopup();
  }

  showPopup() {
    const { name, minutes, city } = this.popupService.getNextPopupData();
    this.name = name;
    this.minutes = minutes;
    this.city = city;

    const popup = document.getElementById('popup');
    if (popup) {
      popup.classList.add('popup-show');
    }

    setTimeout(() => {
      if (popup) {
        popup.classList.remove('popup-show');
      }
      setTimeout(() => this.showPopup(), 5000);
    }, 3000);
  }
}
